  function sortBrFlr(flrArr) {
    let jisang = [];
    let jiha = [];

    for (let i = 0; i < flrArr.length; i++) {
      let flr = flrArr[i];
      flr.flrNo = Number(flr.flrNo); // ì¸µë²ˆí˜¸ë¥¼ string -> numberë¡œ ë³€ê²½
      flr.area = Number(flr.area); // ì¸µë©´ì ì„ string -> numberë¡œ ë³€ê²½
      if (flr.flrGbCd == 20) {
        jisang.push(flr);
      } else if (flr.flrGbCd == 10) {
        jiha.push(flr);
      }
    }

    jisang = jisang.sort(sortDESC("area")).sort(sortDESC("flrNo"));
    jiha = jiha.sort(sortDESC("area")).sort(sortACN("flrNo"));

    return jisang.concat(jiha);
  }

  // ìˆœì„œëŒ€ë¡œ ì •ë ¬
  function sortACN(prop) {
    return function (a, b) {
      if (a[prop] > b[prop]) {
        return 1;
      } else if (a[prop] < b[prop]) {
        return -1;
      }
      return 0;
    };
  }

  // ë°˜ëŒ€ë¡œ ì •ë ¬
  function sortDESC(prop) {
    return function (a, b) {
      if (a[prop] > b[prop]) {
        return -1;
      } else if (a[prop] < b[prop]) {
        return 1;
      }
      return 0;
    };
  }

  async function getStanReginCd(jibun) {
