  function setBunJi(jibun) {
    // ìž…ë ¥ëœ ì§€ë²ˆì£¼ì†Œë¡œ ë²ˆì§€ í• ë‹¹
    let jibunArr = jibun.split(" ");
    let bunji = jibunArr[jibunArr.length - 1];
